.b-posts {
  display: flex;
  flex-wrap: wrap; }

.b-posts__list {
  margin: 0;
  padding: 0;
  text-indent: 0;
  list-style: none;
  list-style-position: outside;
  min-width: 50%;
  width: 500px;
  flex-grow: 1;
  margin-right: 20px;
  margin-bottom: 40px; }

.b-posts__item-link {
  text-decoration: none;
  border-bottom: 1px dotted; }

.b-posts__form-wr {
  flex-grow: 1;
  display: flex;
  flex-direction: column; }

.b-posts__btn-open-form {
  margin-left: auto; }

.b-posts__form-label {
  display: flex;
  flex-direction: column;
  max-width: 400px; }
  .b-posts__form-field--message .b-posts__form-label {
    max-width: 100%; }

.b-posts__form-label-text {
  font-weight: bold; }

.b-posts__form-input-date {
  height: 46px;
  border-radius: 5px;
  border: none;
  background-color: #fff;
  padding-left: 20px;
  padding-right: 20px; }

.b-posts__form-text-area.ui-textarea {
  min-height: 150px; }

.b-posts__form-footer {
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-start;
  margin-left: -5px;
  margin-right: -5px;
  max-width: 1000px; }

.b-posts__form-btn {
  flex-grow: 1;
  margin: 0 5px 10px; }



