.b-admin-nav__list {
  margin: 0;
  padding: 0;
  text-indent: 0;
  list-style: none;
  list-style-position: outside;
  display: flex;
  background: #f0efe9; }

.b-admin-nav__item {
  flex-basis: 180px;
  border-left: 1px solid #fff;
  border-right: 1px solid #fff;
  transition: background-color 100ms; }
  .b-admin-nav__item:first-of-type {
    border-left: none; }

.b-admin-nav__item--active {
  background-color: #fff; }

.b-admin-nav__item-link {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 60px;
  text-decoration: none; }



