.ui-button {
  height: 46px;
  border-radius: 5px;
  border: none;
  background-color: #6c9c5a;
  color: #fff; }

.ui-button--info {
  background-color: #6da3bd; }

.ui-button--default {
  background-color: transparent;
  color: inherit;
  border: 1px solid; }

.ui-button--warning {
  background-color: #373e42; }

.ui-button--danger {
  background-color: #e44845; }

.ui-button__text {
  padding: 1px 20px;
  font-weight: 500; }



