.b-admin-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  overflow: hidden;
  background: #4e8839;
  color: #fff; }

.b-admin-header__title {
  font-size: 28px;
  font-weight: 500; }

.b-admin-header__link {
  opacity: 0.7;
  transition: opacity 100ms; }
  .b-admin-header__link:hover {
    opacity: 1; }



