.b-admin-skills__row-new-group {
  display: flex;
  position: relative;
  height: 46px;
  margin-bottom: 80px;
  overflow: hidden; }

.b-admin-skills__btn-show-new-group.left-enter-active, .b-admin-skills__btn-show-new-group.left-leave-active {
  transform: translateX(0);
  transition: transform 300ms;
  transition-delay: 900ms; }

.b-admin-skills__btn-show-new-group.left-enter, .b-admin-skills__btn-show-new-group.left-leave-to {
  transform: translateX(-100%);
  transition-delay: 0s;
  position: absolute; }

.b-admin-skills__form-new-group {
  overflow: hidden; }
  .b-admin-skills__form-new-group.left-enter-active, .b-admin-skills__form-new-group.left-leave-active {
    transform: translateX(0);
    transition: transform 900ms;
    transition-delay: 300ms; }
  .b-admin-skills__form-new-group.left-enter, .b-admin-skills__form-new-group.left-leave-to {
    transform: translateX(-100%);
    transition-delay: 0s;
    position: absolute; }

.b-admin-skills__group {
  margin-bottom: 80px; }

.b-admin-skills__group-list {
  margin: 0;
  padding: 0;
  text-indent: 0;
  list-style: none;
  list-style-position: outside;
  margin-top: 20px; }



